import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import Menu from "../views/Menu.vue";
import Store from "../views/Store.vue";
import Angebot from "../views/Angebot.vue";
import Jobs from "../views/Jobs.vue";
import Anmeld from "../views/Anmeld.vue";
import Pasta from "../views/Pasta.vue";
import Snacks from "../views/Snacks.vue";
import Dessert from "../views/Dessert.vue";
import Bestell from "../views/Bestell.vue";
import Registir from "../views/Registir.vue";
import Online from "../views/Online.vue";
import Liefer from "../views/Liefer.vue";
import Abholen from "../views/Abholen.vue";

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/menu",
    name: "Menu",
    component: Menu,
  },
  {
    path: "/store",
    name: "Store",
    component: Store,
  },
  {
    path: "/angebot",
    name: "Angebot",
    component: Angebot,
  },
  {
    path: "/jobs",
    name: "Jobs",
    component: Jobs,
  },
  {
    path: "/Anmeld",
    name: "Anmeld",
    component: Anmeld,
  },
  {
    path: "/pasta",
    name: "Pasta",
    component: Pasta,
  },
  {
    path: "/snacks",
    name: "Snacks",
    component: Snacks,
  },
  {
    path: "/dessert",
    name: "Dessert",
    component: Dessert,
  },
  {
    path: "/bestell",
    name: "Bestell",
    component: Bestell,
  },
  {
    path: "/registir",
    name: "Registir",
    component: Registir,
  },
  {
    path: "/online",
    name: "Online",
    component: Online,
  },
  {
    path: "/liefer",
    name: "Liefer",
    component: Liefer,
  },
  {
    path: "/Abholen",
    name: "Abholen",
    component: Abholen,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
